* {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  margin: 0;
  background-color: #022a58;
  font-family: 'Anton', sans-serif;
  color: white;
  display: grid;
  grid-template-rows: 70px 1fr 70px;
  justify-content: stretch;
  height: 100%;
}

.body-light {
  background-color: white;
}

header {
  background-color: rgba(5, 0, 22, 0.3);
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  grid-row: 1/2;
  padding-top: 10px;
}

.background-blue {
  background-color: #022a58;
  color: white;
}

main {
  overflow: scroll;
  grid-row: 2/3;
}

footer {
  background-color: rgba(5, 1, 20, 0.3);
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
  grid-row: 3/4;
  align-self: stretch;
  padding-top: 15px;
}

.header__headline {
  align-self: center;
  margin: auto;
  text-align: center;

  &--gradient {
    background-image: linear-gradient(
      45deg,
      rgba(255, 134, 90, 1) 0%,
      rgba(255, 154, 114, 1) 100%
    );
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}

.nav {
  display: flex;
  justify-content: space-around;

  &__link-1 {
    grid-column: 1/2;
  }

  &__link-2 {
    grid-column: 2/3;
  }

  &__link-3 {
    grid-column: 3/4;
  }

  &__link-4 {
    grid-column: 4/5;
  }
}
